<template>
	<view class="emoji" :style="{
		height
	}">
		<swiper :style="{
			height
		}" :current="currentIndex" @change="changeIndex">
			<swiper-item v-for="(el,index) in emoji" :key="index" class="emoji_item">
				<div class="emoji-content">
					<view v-for="(item,i) in el" :key="i" style="display: inline-block;" class="emoji-swiper-item" @click="click(item)"> 
						{{item}}
					</view>
				</div>
			</swiper-item>
		</swiper>
		
		<view class="emoji-button">
			<view class="emoji-swiper-back" :animation="admation" :style="{
				left:currentX + 'px',
			}"></view>
			<scroll-view class="emoji-scroll-view" scroll-x>
				<text class="iconfont icon-biaoqing emoji-biaoq" @click="switer(0)"></text>
				<text class="iconfont icon-dongwu emoji-biaoq" @click="switer(1)"></text>
				<text class="iconfont icon-shoucang emoji-biaoq" @click="switer(2)"></text>
			</scroll-view>
		</view>
		
	
		<view class="emoji-send-content" v-show="currentIndex<3">
			<button type="default" class="emoji-sedn-button" style="background-color: #4CD964;color: #FFFFFF;" @click="clickSend">ÂèëÈÄÅ</button>
		</view>
	</view>
</template>

<script>
	// import emoji from "../js_sdk/m-emoji/m-emoji_2.0.0/emoji.js"
	export default{
		props:{
			height:{
				type:String,
				default:'230px'
			}
		},
		data(){
			return {
				emoji:[
					["üòÉ üòÑ üòÅ üòÜ üòÖ ü§£ üòÇ üôÇ üôÉ üòâ üòä üòá ü•∞ üòç ü§© üòò üòóÔ∏è üòô üòã üòõ üòú ü§™ üòù ü§ë ü§ó ü§≠ ü§´ ü§î ü§ê ü§® üòê üòë üò∂ üòè üòí üôÑ üò¨ ü§• üòå üòî üò™ ü§§ üò¥ üò∑ ü§í ü§ï ü§¢ ü§Æ ü§ß ü•µ ü•∂ ü•¥ üòµ ü§Ø ü§† ü•≥ üòé ü§ì üßê üòï üòü üôÅ üòØ üò≤ üò≥ ü•∫ üò¶ üòß üò® üò∞ üò• üò¢ üò≠ üò± üòñ üò£ üòû üòì üò© üò´ ü•± üò§ üò° üò† ü§¨"],
					['üê∂ üôà üôâ üôä üí• üí´ üí¶ üí® üêµ üêí ü¶ç ü¶ß üêï ü¶Æ üêï ‚Äçü¶∫ üê© üê∫ ü¶ä ü¶ù üê± üêà üêà ‚Äç‚¨õ ü¶Å üêØ üêÖ üêÜ üê¥ üêé ü¶Ñ ü¶ì ü¶å üêÆ üêÇ üêÉ üêÑ üê∑ üêñ üêó üêΩ üêè üêë üêê üê™ üê´ ü¶ô ü¶í üêò ü¶è ü¶õ üê≠ üêÅ üêÄ üêπ üê∞ üêá üêø ü¶î ü¶á üêª üêª üêº ü¶• ü¶¶ ü¶® ü¶ò ü¶° üêæ ü¶É üêî üêì üê£ üê§ üê• üê¶ üêß üïä ü¶Ü ü¶¢ ü¶â ü¶© ü¶ö ü¶ú üê∏ üêä üê¢ ü¶é üêç üê≤ üêâ ü¶ï ü¶ñ üê≥ üêã üê¨ üêü üê† üê° ü¶à üêô üêö üêå ü¶ã üêõ üêú üêù üêû ü¶ó ü¶ü ü¶† ü¶Ä ü¶û ü¶ê ü¶ë'],
					['üçá üçà üçâ üçä üçã üçå üçç ü•≠ üçé üçè üçê üçë üçí üçì ü•ù üçÖ ü•• ü•ë üçÜ ü•î ü•ï üåΩ üå∂ ü•¨ ü•¶ üßÑ üßÖ üçÑ ü•ú üå∞ üçû ü•ê ü•ñ ü•® ü•Ø ü•û üßá üßÄ üçñ üçó ü•© ü•ì üçî üçü üçï üå≠ ü•™ üåÆ üåØ ü•ô üßÜ ü•ö üç≥ ü•ò üç≤ ü•£ ü•ó üçø üßà üßÇ ü•´ üç± üçò üçô üçö üçõ üçú üçù üç† üç¢ üç£ üç§ üç• ü•Æ üç° ü•ü ü•† ü•° ü¶™ üç¶ üçß üç® üç© üç™ üéÇ üç∞ üßÅ ü•ß üç´ üç¨ üç≠ üçÆ üçØ üçº ü•õ ‚òï üçµ üç∂ üçæ üç∑ üç∏ üçπ üç∫ üçª ü•Ç ü•É ü•§ üßÉ üßâ üßä ü•¢ üçΩ üç¥ ü•Ñ']
				],
				currentIndex:0,
				currentX:0,
				admation:''
			}
		},
		methods:{
			spliteEmoji(){
				this.emoji.forEach((el,index)=>{
					this.emoji[index] = el[0].split(' ')
				})
			},
			click(val){
				this.$emit('click',val);
			},
			// ÂàáÊç¢content
			switer(index){
				this.getNodeInfo(index);
			
						
				this.currentIndex = index;
			},
			changeIndex(e){
				this.currentIndex = e.detail.current
				this.getNodeInfo(this.currentIndex)
			},
			getNodeInfo(index){
				const query = uni.createSelectorQuery().in(this)
				let data;
				query.selectAll('.emoji-biaoq').boundingClientRect(val =>{
					data = val[index];
		
					// #ifdef H5
						this.currentX  = data.left - 1
					// #endif 
							
					// #ifdef MP-WEIXIN
						this.currentX  = data.left - 3
					// #endif
					
			
					let admation = uni.createAnimation({
						duration:200,
						timingFunction:'linear'
					})
					
					this.admation = admation
							
					admation.left(this.currentX).step();
					
					this.admation = admation.export()
			
				}).exec()
			},
			
			// ÂèëÈÄÅÊ∂àÊÅØ‰∫ã‰ª∂
			clickSend(e){
				this.$emit('clickSend',e)
			}
		},
		created(){
			this.spliteEmoji();
			let admation = uni.createAnimation({
				duration:500,
				timingFunction:'linear'
			})
			
			this.admation = admation
		},
		mounted(){
			this.getNodeInfo(this.currentIndex);
		}
	}
</script>
<style lang="scss">
	.emoji{
		position: relative;
		background-color: #FFFFFF;
		overflow: hidden;
	}
	
	.emoji-swiper-back{
		width: 50px;
		height: 30px;
		background-color: rgba(127, 127, 127,.3);
		position: absolute;
		top: 0;
		z-index: $uni-zindex-base;
		border-radius: $uni-border-radius-lg;
		top: 50%;
		transform: translateY(-50%);
	}
	
	.emoji_item{
		height: 100%;
		overflow:auto;
	}
	
	.emoji-content{
		width: 100%;
		display: flex;
		flex-flow:  wrap;
		padding-bottom: 70px;
		padding-top: 40px;
	}
	
	.emoji-content::after{
		width: 10%;
	}
	
	.emoji-swiper-item{
		text-align: center;
		width: 10%;
		margin: 3.3%;
		font-size: 20px;
	}
	
	.emoji-button{
		height: 40px;
		position: absolute;
		width: 100%;
		top: -1px;
		background-color: #FFFFFF;
		border-bottom: solid 1px $uni-border-color;
	}
	
	
	.emoji-scroll-view{
		height: 100%;
		line-height: 40px;
		white-space: nowrap;
		display: flex;
		align-items: center;
		.emoji-biaoq{
			display: inline;
			height: 30px;
			font-size: 25px;	
			align-items: center;
			padding: 0 10px;
			margin: 0 20rpx;
			border-radius: 50px;
		
		}
		::-webkit-scrollbar{
			display: none;
		}
	}
	
	
	.emoji-sedn-button{
		display: inline-block;
		font-size: 14px;
		border-radius: $uni-border-radius-lg;
		min-width: 30px;
		box-shadow: 1px 1px 5px 1px rgba(0,0,0,.2);
		margin: 0 15rpx;
	}
	
	
	.emoji-send-content{
		position: absolute;
		bottom: 20px;
		right: 0;
	
	}
	
	
	
</style>
